.button {
  background: #76b3fa;
  border-radius: 100px;
  padding: 20px 60px;
  color: #fff;
  text-decoration: none;
  font-size: 1.45em;
  margin: 0 15px; }

/* Hover state animation applied here */
.button:hover {
  -webkit-animation: hover 1200ms linear 2 alternate;
  animation: hover 1200ms linear 2 alternate; }

/* Active state animation applied here */
.button:active {
  -webkit-animation: active 1200ms ease 1 alternate;
  animation: active 1200ms ease 1 alternate;
  background: #5f9be0; }

.gray {
  background: #d2d2d2; }

.gray:active {
  background: #b9b9b9; }

/* Active state animation keyframes below */
@-webkit-keyframes active {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); }
  90% {
    -webkit-transform: scale(0.9, 0.88);
            transform: scale(0.9, 0.88); }
  100% {
    -webkit-transform: scale(0.92, 0.9);
            transform: scale(0.92, 0.9); } }

